<script>
import {AU_EMAIL} from "@/store/Global";

export default {
    name: "InvoiceDetail",
    components: {},
    data() {
        return {
            isPc: this.isPc(),
            printObj: {
                id: "printMe",
                popTitle: 'UnPanel',
                extraCss: '',
                extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>'
            },
            invoiceItem: {},
            payType: {
                0: '',
                1: '',
                2: '',
                3: ''
            },
            loading: true,
            site_name: SITE_NAME,
            intervalId: '',
            iconLoading: false,
            payPerson: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@UnPanel.net') && localStorage.getItem(AU_EMAIL).length > 40 ? 'Anonymous' : localStorage.getItem(AU_EMAIL) : 'Anonymous',
            oldStatus: '',
        }
    },
    mounted() {
        if (this.$route.params.invoiceId != 0) this.getInvoiceDetail();
    },
    destroyed() {
        window.clearInterval(this.intervalId);
    },
    methods: {
        printRow() {
            this.$print(this.$refs.print)
        },
        startPolling() {
            const that = this;
            if (that.intervalId.length === 0) {
                that.startPollingTime = new Date().getTime();
                that.intervalId = setInterval(function () {
                    if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
                        window.clearInterval(that.intervalId);
                    } else {
                        that.getInvoiceDetail();
                    }
                }, 4000);
            }
        },
        getInvoiceDetail() {
            const that = this;
            this.request.getInvoiceDetail(this.$route.params.invoiceId).then(res => {
                that.loading = false;
                if (res.code === 1) {
                    that.invoiceItem = res.data;
                    if (that.oldStatus.length === 0) that.oldStatus = res.data.status;
                    if (res.data.status === 2) {
                        that.iconLoading = false;
                        if (that.oldStatus === 0) {
                            that.oldStatus = res.data.status;
                        }
                        window.clearInterval(that.intervalId);
                    }
                }
            });
        },
        payInvoice(id) {
            this.$router.push({
                path: 'order/pay',
                name: 'payment',
                params: {
                    isRenew: true,
                },
                query: {
                    invoiceId: id,
                }
            });
        },
        countUnpaidAmount() {
            let total = this.countBillTotal();
            this.invoiceItem.transactions.transaction.forEach(item => {
                if (this.invoiceItem.status === 'Refunded' && item.refundid !== 0) {
                    total = Number(total - Number(item.amountout));
                } else if (this.invoiceItem.status === 2 && item.refundid === 0) {
                    total = Number(total - Number(item.amountin));
                }
            });
            return Number(total).toFixed(2);
        },
        countBillTotal() {
            let total = 0;
            this.invoiceItem.items.item.forEach(item => {
                total = Number(item.amount) + total;
            });
            return Number(total).toFixed(2);
        },
        openZw() {

        },
    }
}
</script>

