<template>
    <div class="dialog-container">
        <div class="dialog-bg"></div>
        <div class="mb-service-dialog-box" @click="errorMessage = ''">
            <a @click="closeDialog()"></a>
            <span class="title">{{isClickShare ? '' : isClickBindEmail ? '' : ''}}</span>
            <div class="share-box" v-if="isClickShare">
                <!--<a @click="isClickShare = false"></a>-->
                <div class="card-box-bg panel-address-box panel-address-box-margin">
                    <span class="share-first-row"></span>
                    <span class="share-sec-row"> {{ selectedItem.aff_text}} </span>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span></span>
                        </template>
                        <span class="share-third-row" v-clipboard:copy="selectedItem.aff_url"></span>
                    </a-tooltip>
                </div>
<!--                <div class="card-box-bg  panel-address-box">-->
<!--                    <span class="share-first-row"></span>-->
<!--                    <span class="share-sec-row"></span>-->
<!--                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>-->
<!--                        <template slot="title">-->
<!--                            <span></span>-->
<!--                        </template>-->
<!--                        <span class="share-third-row" v-clipboard:copy="selectedItem.usageDashboardUrl"></span>-->
<!--                    </a-tooltip>-->
<!--                </div>-->
            </div>
            <div class="share-box" v-if="isClickBindEmail">
                <div class="card-box-bg  panel-address-box">
                    <span class="email-first-row"></span>
                    <span class="email-sec-row"></span>
                    <div class="email-third-row">
                        <a-input placeholder="" :class="{'email-input-error':emailErrorMsg === 'email'}" v-model="selectedItem.email" @change="emailChange" :disabled="selectedItem.emailStatus === 'Binded'"></a-input>
                        <span class="email-center" v-if="isStartBindEmail && !isCountTimeEnd">{{secondNum}}(s)</span>
                        <span class="email-right" @click="startBindEmail()" v-if="isStartBindEmail && isCountTimeEnd"></span>
                        <span class="email-right" @click="clickUnbindEmail" v-if="selectedItem.emailStatus === 'Binded'"></span>
                        <span class="email-right" @click="startBindEmail()" v-if="selectedItem.emailStatus !== 'Binded' && !isStartBindEmail"></span>
                        <span class="email-right" @click="revokeEmail" v-if="selectedItem.emailStatus !== 'Binded' && isStartBindEmail"></span>
                    </div>
                    <span class="email-four-row" :class="{'error-tips': emailErrorMsg !== ''}">{{emailErrorMsg}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    name: "AccessPointDialog",
    data(){
        return {
            isClickShare: false,
            isClickBindEmail: false,
            isStartBindEmail: false,
            emailErrorMsg: '',
            emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
            secondNum: 30,
        }
    },
    props: {
        clickShare: {},
        clickBindEmail: {},
        selectedItem:{},
    },
    watch: {
        clickShare: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.isClickShare = value;
                }
            }
        },
        clickBindEmail: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.isClickBindEmail = value;
                }
            }
        },
        selectedItem: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    console.log('value', value);
                }
            }
        },
    },
    methods:{
        emailChange() {
            if (this.selectedItem.email) {
                this.emailErrorMsg = ''
            }
        },
        startBindEmail() {
            if (this.selectedItem.email) {
                if (this.emailReg.test(this.selectedItem.email)) {
                    let that = this;
                    this.$request.bindEmail(this.selectedItem.email, function (data) {
                        if (data.status === 200) {
                            that.isStartBindEmail = true;
                            that.startCountTime();
                            that.emailErrorMsg = '';
                        } else {
                            that.emailErrorMsg = '';
                        }
                    });
                } else {
                    this.emailErrorMsg = 'email';
                }
            } else {
                this.emailErrorMsg = 'email';
            }
        },
        revokeEmail() {
            if (this.checkTimer > 0) {
                window.clearInterval(this.checkTimer);
                this.checkTimer = 0;
                this.totalCheckCount = 0;
            }
            this.isStartBindEmail = false;
            this.selectedItem.email = '';
            this.emailErrorMsg = '';
        },
        clickUnbindEmail() {
            let that = this;
            this.$request.unBindEmail(this.selectedItem.email, function (data) {
                if (data.status === 200) {
                    that.getServiceInfo();
                }
            });
        },
        startCountTime() {
            this.isCountTimeEnd = false;
            let test = setInterval(() => {
                this.secondNum--;
                if (this.secondNum === 0 || this.selectedItem.emailStatus === 'Binded' || !this.isStartBindEmail) {
                    window.clearInterval(test);
                    this.secondNum = 30;
                    this.isCountTimeEnd = true;
                    if (this.selectedItem.emailStatus === 'Binded') {
                        this.isStartBindEmail = false;
                    }
                } else if (this.secondNum === 10) {
                    this.emailErrorMsg = '';
                }
                if (this.secondNum === 25) {
                    if (this.checkTimer === 0) {
                        this.checkTimer = setInterval(() => {
                            this.totalCheckCount++;
                            if (!this.isStartBindEmail || this.selectedItem.emailStatus === 'Binded' || this.totalCheckCount === 15 || this.checkTimer === 0) {
                                window.clearInterval(this.checkTimer);
                                this.checkTimer = 0;
                                this.totalCheckCount = 0;
                            } else {
                                this.getServiceInfo();
                            }
                        }, 4000);
                    }
                }
            }, 1000);
        },
        closeDialog(){
            this.errorMessage = '';
            this.$emit('closeDialog',false,'')
        }
    }
}
</script>

