<template>
  <div>
    <div class="article-loading" :style="{'min-height':isSimpleArticle ? '500px' : '100vh'}" v-if="isLoading && isPc">
      <a-spin>
        <a-icon slot="indicator" type="loading" :style="{'font-size': isSimpleArticle ? '80px' : '120px'}" spin/>
      </a-spin>
    </div>
    <div class="article-loading" :style="{'min-height': '100vh'}" v-if="isLoading && !isPc">
      <a-spin>
        <a-icon slot="indicator" type="loading" :style="{'font-size': '80px'}" spin/>
      </a-spin>
    </div>
    <p class="tutorial-article-name" v-if="!isLoading">{{ name }}</p>
    <div v-if="!isLoading" class="article-body" :class="{'article-body-mb':!isPc}" v-html="articleBody">
    </div>
  </div>

</template>

<script>
import $ from 'jquery';

export default {
  name: 'SimpleArticle',
  data() {
    return {
      isPc: this.isPc(),
      articleBody: '',
      articleId: 0,
      isLoading: true,
      name: '',
    };
  },
  props: {
    article: {},
    isSimpleArticle: {},
  },
  watch: {
    article: {
      immediate: true,
      handler(val) {
        if (val) {
          this.isLoading = true;
          let that = this;
          let data = {};
          if (val[0] === '/') {
            val = val.substr(1, val.length);
          }
          data = require('../' + val);
          that.name = data.article.name;
          that.articleBody = data.article.body;
          that.articleHandler(data.article.body);
        }
      }
    },
    isSimpleArticle: {
      immediate: true,
      handler(val) {
        if (val !== undefined) {
          this.isSimpleArticle = val;
        }
      }
    }
  },
  methods: {
    articleHandler(data) {
      const that = this;
      setTimeout(function () {
        let aList = document.getElementsByTagName('a');
        if (aList.length > 0) {
          that.addEventForLastA();
        }
      }, 500);
      // setting the animation
      setTimeout(function () {
        $('.accordion__item-title')
            .on('click', function () {
              let $title = $(this);
              $title.toggleClass('accordion__item-title--active');
              $title.parents('.accordion__item')
                  .find('.accordion__item-content')
                  .slideToggle();
            });
        let HC_SETTINGS = {
          css: {
            activeClass: 'is-active',
            hiddenClass: 'is-hidden'
          }
        };
        $('.tabs-link')
            .click(function (e) {
              e.preventDefault();
              let $link = $(this);
              let tabIndex = $link.index();
              let $tab = $link.parents('.tabs')
                  .find('.tab')
                  .eq(tabIndex);
              $link.addClass(HC_SETTINGS.css.activeClass)
                  .siblings()
                  .removeClass(HC_SETTINGS.css.activeClass);
              $tab.removeClass(HC_SETTINGS.css.hiddenClass)
                  .siblings('.tab')
                  .addClass(HC_SETTINGS.css.hiddenClass);
            });
      }, 1000);
      this.isLoading = false
    },
    addEventForLastA() {
      if (this.isSimpleArticle || !this.isSimpleArticle) {
        let aList = Array.from(document.getElementsByTagName('a'));
        aList.forEach((item, p) => {
          let aTag = aList[p];
          let url = aTag.href;
          if (url.toString().length > 0 && /(.*)articles{1}\//.test(url)) {
            $(`a:eq(${p})`).removeAttr("href");
            $(`a:eq(${p})`).removeAttr("target");
            $(`a:eq(${p})`).removeAttr("rel");
            const that = this;
            aTag.addEventListener('click', function () {
              let id = url.substring(url.lastIndexOf("/") + 1);
              if (that.isSimpleArticle) {
                that.$emit('detailedTutorialId', id);
              } else {
                that.$emit('otherTutorialId', id);
              }
            });
          }
        });
      }
    }
  }
};
</script>

