<template>
    <div class="articles-view-container">
        <div class="articles-view-second-box" v-if="isPc">
            <div :class="{'articles-view-detail-box': isPc,'articles-view-detail-box-mobile': !isPc}">
                <div class="article-close" @click="hideDetailedArticle" v-if="showClose || otherUrl.length > 0"></div>
                <div class="tutorial-article">
                    <div class="articles-detail">
                        <SimpleArticle :article="otherUrl.length === 0 ? url : otherUrl" :isSimpleArticle="false" v-on:otherTutorialId="otherTutorialId"></SimpleArticle>
                    </div>
                </div>
            </div>
        </div>
        <div class="detailed-dialog-mobile" v-if="!isPc">
            <div class="detailed-article-box" @click="errorMessage = ''">
                <div class="close-a" @click="hideDetailedArticle"></div>
                <div class="tutorial-article">
                    <div class="articles-detail">
                        <SimpleArticle :article="otherUrl.length === 0 ? url : otherUrl" :isSimpleArticle="false" v-on:otherTutorialId="otherTutorialId"></SimpleArticle>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SimpleArticle from "../views/SimpleArticle";
export default {
    name: 'DetailedArticle',
    components: {
        SimpleArticle,
    },
    data() {
        return {
            url:'',
            showClose: true,
            otherUrl: '',
            isPc : this.isPc(),
        };
    },
    props: {
        detailedUrl: {},
    },
    watch: {
        detailedUrl: {
            immediate: true,
            handler(val) {
                if (val !== undefined) {
                    this.showClose = true;
                    this.url = val;
                }
            }
        },
    },
    mounted() {
        if (this.$route.params.articleId !== undefined) {
            this.articleId = this.$route.params.articleId.toString();
            if (this.articleId) {
                let articleArray;
                if (this.articleId.indexOf("-") > -1) {
                    articleArray = this.articleId.split("-")
                }
                if (articleArray != null && articleArray.length > 1) {
                    this.articleId = articleArray[0]
                }
                this.showClose = false;
                this.url = `api/v2/help_center/zh-tw/articles/${this.articleId}.json`;
            }
        }
    },
    methods: {
        otherTutorialId(id) {
            this.otherUrl = `api/v2/help_center/zh-tw/articles/${id}.json`;
        },
        hideDetailedArticle() {
            if (this.otherUrl.length > 0) {
                this.otherUrl = '';
            } else {
                this.$emit('hideDetailedArticle',true);
            }
        }
    }
}
</script>

<style lang="scss">
.articles-view-container {
    width: 100%;
    background: #F6F8FA;
    min-height: 100%;
    height: fit-content;
    overflow: auto;
    text-align: justify;
    .articles-view-second-box {
        position: absolute;
        width: 100%;
        background: #F6F8FA;
    }
    .articles-view-detail-box {
        display: flex;
        flex-direction: column;
        width: 1080px;
        min-height: 100vh;
        margin: 20px auto;
        box-shadow: 0 2px 6px rgba(234, 238, 246, 0.6);
        border-radius: 8px;
        background-color: #fff;
        position: relative;
        padding: 39px 37px;
        overflow: auto;
        text-align: justify;
        .article-close {
            position: absolute;
            top: 0px;
            right: -1px;
            width: 40px;
            height: 39px;
            cursor: pointer;
            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close.svg') center no-repeat;
            &:hover {
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close-hover.svg') center no-repeat;
            }
        }
        .tutorial-article-name {
            font-size: 24px;
            font-weight: 400;
            color: #383E44;
            margin-bottom: 40px;
        }
    }
    .articles-view-detail-box-mobile {
        display: flex;
        flex-direction: column;
        width: 100%;
        min-height: 100vh;
        margin: 20px auto;
        box-shadow: 0 2px 6px rgba(234, 238, 246, 0.8);
        border-radius: 8px;
        background-color: #fff;
        position: relative;
        padding: 39px 37px;
        overflow: auto;
        text-align: justify;
        .article-close {
            position: absolute;
            top: 0px;
            right: 0;
            width: 1.067rem;
            height: 1.067rem;
            cursor: pointer;
            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close.svg') center no-repeat;
            &:hover {
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close-hover.svg') center no-repeat;
            }
        }
        .tutorial-article-name {
            font-size: 0.587rem;
            font-weight: 400;
            color: #383E44;
            margin-bottom: 0.853rem;
        }
    }
    .detailed-dialog-mobile {
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(31, 34, 46, 0.8);
        width: 100%;
        min-height: 100%;
        z-index: 1;
        .detailed-article-box {
            background-color: white;
            border-radius: 0.427rem;
            width: 100%;
            overflow: auto;
            height: 100%;
            padding: 0.533rem;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: 1.173rem;
            transform: translate(-50%, -50%);
            display: flex;
            flex-direction: column;
            .close-a {
                position: absolute;
                top: 0.733rem;
                right: 0.533rem;
                width: 0.427rem;
                height: 0.427rem;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close-x.svg') center no-repeat;
                background-size: contain;
            }
            .tutorial-article-name {
                font-size: 0.587rem;
                font-weight: 400;
                color: #383E44;
                margin: 0 0.733rem 0.853rem 0;
            }
        }
    }
}
</style>
