<template>
    <div class="dialog-container">
        <div class="dialog-bg"></div>
        <div class="switch-service-dialog-box dialog-box" v-if="isPc">
            <!--<a @click="closeDialog()"></a>
            <span class="title-span"></span>-->
            <div class="first-row">
                <span></span>
                <i @click="closeDialog()"></i>
            </div>
            <div class="loading" v-if="loading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div v-for="(item,index) in dataList" :key="index">
                <div class="item-box" :class="{'item-box-checked': clickItem === index}" @click="clickItem = index">
                    <span>{{item.planName.en}} ({{item.id}})</span>
                    <a-divider style="background-color: #E5E9ED;margin: 20px 0px;"/>
                    <div style="display:flex;">
                        <span class="first-span">{{item.speedLimit === -1? '': item.speedLimit}} M</span>
                        <span class="first-span" style="margin:0px 51px;">{{item.sessionLimit === -1? '': item.sessionLimit}}</span>
                        <span class="first-span" style="width:84px;margin-right:50px;">{{formatDate(item.nextResetTime)}}</span>
                        <span class="first-span" style="width:82px;text-align:right;">{{formatDate(item.expiredTime)}}</span>
                    </div>
                    <div style="display:flex;margin-top:8px;">
                        <span class="first-span sec-span"></span>
                        <span class="first-span sec-span" style="margin:0px 51px;"></span>
                        <span class="first-span sec-span" style="width:84px;margin-right:50px;"></span>
                        <span class="first-span sec-span" style="width:82px;"></span>
                    </div>
                </div>
            </div>
            <div v-if="dataList.length > 0">
                <a-button type="primary" @click="confirmSwitch" :disabled="dataList.length === 0"></a-button>
            </div>
            <div class="error-message">
                <span style="position: absolute">{{errorMessage}}</span>
            </div>
        </div>
        <div class="switch-service-dialog-box-mobile" v-if="!isPc">
            <div class="first-row">
                <span></span>
                <i @click="closeDialog()"></i>
            </div>
            <div class="loading" v-if="loading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div v-for="(item,index) in dataList" :key="index">
                <div class="item-box" :class="{'item-box-checked': clickItem === index}" @click="clickItem = index">
                    <span style="font-weight: 500;font-size: 0.453rem;">{{item.planName.en}} ({{item.id}})</span>
                    <a-divider style="background-color: #E5E9ED;margin: 0.373rem 0px;"/>
                    <div style="display:flex;">
                        <span class="item-title-span"></span>
                        <span class="item-title-span"></span>
                    </div>
                    <div style="display:flex;margin-top: 0.107rem;">
                        <span class="item-title-span item-content-span">{{item.speedLimit}} M</span>
                        <span class="item-title-span item-content-span">{{item.sessionLimit}}</span>
                    </div>
                    <div style="display:flex;margin-top: 0.373rem;">
                        <span class="item-title-span"></span>
                        <span class="item-title-span"></span>
                    </div>
                    <div style="display:flex;margin-top: 0.107rem;">
                        <span class="item-title-span item-content-span">{{formatDate(item.nextResetTime)}}</span>
                        <span class="item-title-span item-content-span">{{formatDate(item.expiredTime)}}</span>
                    </div>
                </div>
            </div>
            <div style="margin: 0.533rem;" v-if="dataList.length > 0">
                <a-button type="primary" @click="confirmSwitch" :disabled="dataList.length === 0"></a-button>
            </div>
            <div class="error-message">
                <span style="position: absolute">{{errorMessage}}</span>
            </div>
        </div>
    </div>
</template>
<script>
import {AU_EMAIL, AU_EMAIL_STATUS, AU_SELECTED_SERVICE} from "@/store/Global";

export default {
    name: "SwitchServiceDialog",
    components: {},
    data() {
        return {
            loading: true,
            clickItem: -1,
            dataList: [],
            errorMessage: '',
            isPc: this.isPc(),
        }
    },
    created() {
        const that = this;
        this.$request.auGetAllServices('', '', function (data) {
            that.loading = false;
            if (data && (data.status === 200 || data.status === 201) && data.data) {
                localStorage.setItem(AU_EMAIL, data.data.email);
                localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                if (data.data.jjService && data.data.jjService.length > 0) {
                    that.dataList = data.data.jjService;
                    if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                        let selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                        that.dataList.forEach((item, index) => {
                            if (item.speedLimit === -1) {
                                item.speedLimit = ''
                            } else {
                                item.speedLimit = item.speedLimit / Math.pow(1024, 2) * 8
                            }
                            if (item.sessionLimit === -1) {
                                item.sessionLimit = ''
                            }
                            if (item.id === selectedService.id) {
                                that.clickItem = index;
                            }
                        });
                    }
                }
            }
        });
    },
    methods: {
        formatDate(value) {
            let date = new Date(value);
            let y = date.getFullYear();
            let MM = date.getMonth() + 1;
            MM = MM < 10 ? ('0' + MM) : MM;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            return y + '-' + MM + '-' + d;
        },
        confirmSwitch() {
            if (this.clickItem !== -1) {
                localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(this.dataList[this.clickItem]));
            }
            this.$emit('closeDialog', true);
        },
        closeDialog() {
            this.$emit('closeDialog', true);
        }
    }
}
</script>

<style lang="scss">
.dialog-container {
    height: 100%;

    .switch-service-dialog-box {
        text-align: center;
        background-color: white;
        border-radius: 4px;
        width: 580px;
        overflow: auto;
        max-height: 90%;
        height: fit-content;
        padding: 40px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        flex-direction: column;

        span {
            color: #616577;
        }

        .first-row {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin-bottom: 12px;
            justify-content: space-between;

            span {
                font-size: 28px;
                color: #242C45;
            }

            i {
                width: 12px;
                height: 12px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close.svg') center no-repeat;
                background-size: contain;
                cursor: pointer;
                margin-top: -50px;
            }
        }

        .loading {
            height: 250px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 25px;
        }

        .item-box {
            border-radius: 8px;
            border: solid 1px #E5E9ED;
            padding: 32px;
            margin-bottom: 24px;
            text-align: left;
            cursor: pointer;

            .first-span {
                width: 58px;
                text-align: left;
                font-size: 16px;
            }

            .sec-span {
                font-size: 14px;
            }

            &:hover {
                border-color: #3963EF;
            }
        }

        .item-box-checked {
            border-color: #3963EF;
        }

        .ant-btn {
            width: 112px;
            height: 40px;
            background-color: #3963EF;
            border-color: #3963EF;
            margin-top: 16px;
        }

        .ant-btn-primary span {
            color: #fff;
        }

        .error-message {
            margin-top: 5px;
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            color: red;
        }
    }

    .switch-service-dialog-box-mobile {
        position: absolute;
        flex-direction: column;
        text-align: center;
        width: 100%;
        min-height: 100%;
        left: 0;
        top: 1.173rem;
        padding: 0.933rem 0.533rem;
        background-color: white;
        border-top-left-radius: 0.427rem;
        border-top-right-radius: 0.427rem;

        span {
            font-size: 0.373rem;
            color: #616577;
        }

        .first-row {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin-bottom: 0.267rem;
            justify-content: space-between;

            span {
                font-size: 0.587rem;
                color: #242C45;
            }

            i {
                margin-top: -1rem;
                width: 0.427rem;
                height: 0.427rem;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-close.svg') center no-repeat;
                background-size: contain;
                cursor: pointer;
            }
        }

        .loading {
            height: 6.667rem;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 0.533rem;
        }

        .item-box {
            border-radius: 0.213rem;
            border: solid 1px #E5E9ED;
            padding: 0.533rem;
            margin-top: 0.533rem;
            text-align: left;
            cursor: pointer;

            .item-title-span {
                width: 50%;
                text-align: left;
                font-size: 0.347rem;
            }

            .item-content-span {
                width: 50%;
                font-size: 0.4rem;
            }

            &:hover {
                border-color: #3963EF;
            }
        }

        .item-box-checked {
            border-color: #3963EF;
        }

        .ant-btn {
            width: 2.667rem;
            height: 1.067rem;
            background-color: #3963EF;
            border-color: #3963EF;
            margin-bottom: 0.533rem;
        }

        .ant-btn-primary span {
            color: #fff;
        }

        .error-message {
            margin-top: 0.133rem;
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            color: red;
        }
    }
}

.dialog-bg {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    background: rgba(31, 34, 46, 0.7);
}
</style>
