                            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/v2rayu-icon.svg') center no-repeat;
                            background-size: contain;

                        }
                        .surge-icon {
                            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ios-surge-icon.svg') center no-repeat;
