<template>
    <div class="service-panel-container" :class="{'header-below': isPc}">
        <div class="second-parent-box">
            <div :class="{'info-error-box':isPc,'info-error-box-mobile':!isPc}" v-if="isLoading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div :class="{'info-error-box':isPc,'info-error-box-mobile':!isPc}"
                 v-if="!isLoading && !selectedItem.shop_id">
                <i class="i-empty"></i>
                <span>{{ $t('my/service:plan.Status-1') }} </span>
                <span class="contact-service-box" @click="goToOrderProduct">{{ $t('my/service:plan.Status-2') }}</span>
            </div>
            <div class="service-detail-box" v-if="isPc && !isLoading && selectedItem.shop_id">
                <div class="detail-left-box">
                    <span class="plan-title">{{ selectedItem.shop_name }} <span
                        class="plan-number">(ID:#{{ selectedItem.shop_id }})</span></span>
                    <span class="plan-expired-date">
                       <span style="color: #ff0000;" v-if="currentTime > selectedItem.expired_at">{{ $t('my/service:plan.Status-3') }}</span>
                       <span
                           v-if="parseInt(currentTime) < parseInt(selectedItem.expired_at)">{{
                               formatDate(selectedItem.expired_at) + ' ' + $t('my/service:plan.Countdown-1')
                           }}</span>
                   </span>
                    <a-button type="primary" class="renew-btn" :loading="iconLoading" @click="goToRenew">
                      {{ $t('my/service:plan.Button-5') }}
                    </a-button>
                    <div style="margin-top: auto;margin-bottom: 0px;"
                         v-if="selectedItem.status !== 'Inactive' && currentTime <= selectedItem.expired_at">
                        <div class="share-reset-row">
                            <div>
                                <span class="share-normal" :class="{'share-click': isClickShare}" @click="shareService">
                                  {{ $t('my/service:plan.Button-6') }}
                                </span>
                                <div style="width: 87px;background: #3963EF;height: 1px;" v-if="isClickShare"/>
                            </div>
                            <div>
                                <span class="reset-normal" :class="{'reset-click': isClickResetService}"
                                      @click="resetService">
                                  {{ $t('my/service:plan.Button-7') }}
                                </span>
                                <div style="width: 87px;background: #3963EF;height: 1px;" v-if="isClickResetService"/>
                            </div>
                        </div>
                        <div v-if="settingsInfo.allowBindEmail">
                            <span class="bind-email-normal" :class="{'bind-email-click': isClickBindEmail}"
                                  @click="bindEmail"></span>
                            <div style="width: 87px;background: #3963EF;height: 1px;" v-if="isClickBindEmail"/>
                        </div>
                    </div>
                    <div style="display:flex;flex-direction: column;margin-top: auto;margin-bottom: 0px;"
                         v-if="selectedItem.status === 'Inactive' && currentTime <= selectedItem.expired_at">
                     <span class="stop-service">{{
                             selectedItem.inactiveReasons.includes('Terminated') ? '' : selectedItem.inactiveReasons.includes('InsufficientStop') ? '' :
                                 selectedItem.inactiveReasons.includes('ManualStop') ? '' : ''
                         }}</span>
                        <span
                            style="color: #A5B1BF;">{{
                                selectedItem.inactiveReasons.includes('HackStop') && selectedItem.inactiveReasons.length === 1 ? '' : ''
                            }}</span>
                        <span class="stop-reset-service" @click="resetService"
                              v-if="selectedItem.inactiveReasons.includes('HackStop') && selectedItem.inactiveReasons.length === 1"></span>
                        <span class="stop-contact-service" @click="goToSupport" v-if="settingsInfo.embedCode !== undefined && settingsInfo.embedCode.CS_CODE !== undefined && settingsInfo.embedCode.CS_CODE.length !== 0
                        && (selectedItem.inactiveReasons.includes('Terminated') || selectedItem.inactiveReasons.includes('InsufficientStop') || selectedItem.inactiveReasons.includes('ManualStop'))"></span>
                    </div>
                </div>
                <div class="detail-right-box">
                    <div class="right-card-box plan-info-box">
                        <div class="card-box-bg info-box-bg flow-detail-box">
                            <div class="flow-right">
                                <span class="number-span">{{ selectedItem.also_traffic }} <span
                                    class="unit-span">GB</span></span>
                                <span class="sec-row">{{ $t('my/service:plan.Text-1') }}</span>
                                <span class="reset-date">{{ selectedItem.reset_time }} {{ $t('my/service:plan.Countdown-2') }}</span>
                            </div>
                            <div class="flow-right">
                               <span
                                   class="number-span">{{
                                       selectedItem.speed_limit > 0 ? selectedItem.speed_limit : ' '
                                   }}
                                   <span class="unit-span">M</span></span>
                                <span class="sec-row">{{ $t('my/service:plan.Text-4') }}</span>
                                <span
                                    class="limit-device"> </span>
                            </div>
                        </div>
                        <div class="card-box-bg speed-box" v-if="trafficArray.length >= 1">
                            <div style="position: absolute;height: 143px;">
                                <div class="speed-line-row" v-for="(item,index) in speedArray" :key="index">
                                    <span style="width: 35px;text-align: right">{{ item }}</span>
                                    <div class="speed-line" v-if="index !== 0"></div>
                                </div>
                            </div>
                            <div
                                style="position: relative;height: 129px;display: flex;flex-direction: row;margin-left:38px;">
                                <div class="x-usage-row" v-for="(item,index) in trafficArray" :key="index">
                                    <div class="histogram-box">
                                        <div class="histogram"
                                             :class="{'last-histogram': index === (trafficArray.length - 1),'addon-histogram':item.isAddon}"
                                             :style="{height: getHistogramH(item) + 'px', width: getHistogramW(index,item)  + 'px'}"></div>
                                        <div class="indicator-box"
                                             :style="{'padding-left': getUsageP(item,index) + 'px'}"
                                             v-if="trafficArray.length > 0 && showUsage(item,index)">
                                            <i></i>
                                            <div class="indicator-line" :style="{height: getUsageH() + 'px'}"></div>
                                        </div>
                                        <div class="tool-tips-group"
                                             :class="{'tool-tips-group-two':trafficArray.length === 1}">
                                            <span v-if="trafficArray.length > 1">{{ item.isAddon ? '' : '' }}{{
                                                    index === (trafficArray.length - 1) ? '' : ' '
                                                }} {{
                                                    item.trafficLimit
                                                }}G{{
                                                    index !== (trafficArray.length - 1) ? '-' + trafficArray[index + 1].trafficLimit + 'G' : ''
                                                }}</span>
                                            <span>{{
                                                    (item.speedLimit === '' || item.speedLimit === 'addon') ? '' : '' + item.speedLimit + 'M'
                                                }}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div
                                style="display:flex;flex-direction:row;justify-content:space-between;margin: 8px -10px 0px 30px;font-size: 13px;">
                                <div
                                    style="display:flex;flex-direction:row;justify-content:space-between;width: 200px;">
                                    <span>0G</span>
                                    <span>{{
                                            (trafficArray[trafficArray.length - 1].trafficLimit === '' || trafficArray[trafficArray.length - 1].trafficLimit === '0') ? '' : (Math.round(trafficArray[trafficArray.length - 1].trafficLimit / 2) !== trafficArray[trafficArray.length - 1].trafficLimit) ? Math.round(trafficArray[trafficArray.length - 1].trafficLimit / 2) + ' G' : ''
                                        }}</span>
                                    <span>{{
                                            (trafficArray[trafficArray.length - 1].trafficLimit === '' || trafficArray[trafficArray.length - 1].trafficLimit === '0') ? '' : trafficArray[trafficArray.length - 1].trafficLimit + 'G'
                                        }}</span>
                                </div>
                                
                            </div>
                        </div>
                        <transition name="slide-fade">
                            <div class="message-box" v-if="isShowMessageBox"><i></i></div>
                        </transition>
                    </div>
                    <div class="right-card-box share-box" v-if="isClickShare">
                        <a @click="isClickShare = false"></a>
                        <div class="card-box-bg panel-address-box panel-address-box-margin">
                            <span class="share-first-row"></span>
                            <span class="share-sec-row" v-html="selectedItem.aff_text"></span>
                            <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                                <template slot="title">
                                    <span></span>
                                </template>
                                <span class="share-third-row" v-clipboard:copy="selectedItem.aff_url"></span>
                            </a-tooltip>
                        </div>
                    </div>
                    <div class="right-card-box bind-email-box" v-if="isClickBindEmail">
                        <a @click="isClickBindEmail = false"></a>
                        <div class="card-box-bg bind-email-content-box">
                            <span class="email-first-row"></span>
                            <span class="email-sec-row"> </span>
                            <div class="email-third-row">
                                <a-input placeholder=""
                                         :class="{'email-input-error':emailErrorMsg === 'email'}"
                                         v-model="selectedItem.email" @change=" emailChange"
                                         :disabled="selectedItem.emailStatus === 'Binded'"></a-input>
                                <span class="email-center"
                                      v-if="isStartBindEmail && !isCountTimeEnd">{{ secondNum }}(s)</span>
                                <span class="email-right" @click="clickBindEmail"
                                      v-if="isStartBindEmail && isCountTimeEnd"></span>
                                <span class="email-right" @click="clickUnbindEmail"
                                      v-if="selectedItem.emailStatus === 'Binded'"></span>
                                <span class="email-right" @click="clickBindEmail"
                                      v-if="selectedItem.emailStatus !== 'Binded' && !isStartBindEmail"></span>
                                <span class="email-right" @click="revokeEmail"
                                      v-if="selectedItem.emailStatus !== 'Binded' && isStartBindEmail"></span>
                            </div>
                            <span class="email-four-row" :class="{'error-tips': emailErrorMsg !== ''}">
                               {{ emailErrorMsg }}
                           </span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="service-detail-box-mobile" v-if="!isPc &&  !isLoading && selectedItem.shop_id">
                <span class="plan-title">{{ selectedItem.shop_name }} <span
                    class="plan-number">(# {{ selectedItem.shop_id }})</span></span>
                <span class="plan-expired-date">
                       <span style="color: red;" v-if="currentTime > selectedItem.expired_at"></span>
                       <span
                           v-if="parseInt(currentTime) < parseInt(selectedItem.expired_at)">{{
                               formatDate(selectedItem.expired_at) + ' '
                           }}</span>
               </span>
                <a-button type="primary" class="renew-btn" :loading="iconLoading" @click="goToRenew"
                          v-if="currentTime > selectedItem.expired_at">{{ '' }}
                </a-button>
                <div class="service-status-box"
                     v-if="selectedItem.status === 'Inactive' && currentTime <= selectedItem.expired_at">
                    <div class="left-status-box">
                       <span class="stop-service">{{
                               selectedItem.inactiveReasons.includes('Terminated') ? '' : selectedItem.inactiveReasons.includes('InsufficientStop') ? '' :
                                   selectedItem.inactiveReasons.includes('ManualStop') ? '' : ''
                           }}</span>
                        <span
                            class="bottom-support">{{
                                selectedItem.inactiveReasons.includes('HackStop') && selectedItem.inactiveReasons.length === 1 ? '' : ''
                            }}</span>
                    </div>
                    <span class="stop-reset-service" @click="resetService"
                          v-if="selectedItem.inactiveReasons.includes('HackStop') && selectedItem.inactiveReasons.length === 1"></span>
                    <span class="stop-contact-service" @click="goToSupport" v-if="settingsInfo.embedCode !== undefined && settingsInfo.embedCode.CS_CODE !== undefined && settingsInfo.embedCode.CS_CODE.length !== 0
                        && (selectedItem.inactiveReasons.includes('Terminated') || selectedItem.inactiveReasons.includes('InsufficientStop') || selectedItem.inactiveReasons.includes('ManualStop'))"></span>
                </div>
                <div class="card-box-bg flow-detail-box">
                    <div class="flow-left">
                        <span class="number-span">{{ selectedItem.also_traffic }} <span
                            class="unit-span">GB</span></span>
                        <span class="sec-row"></span>
                        <span
                            class="reset-date">{{ selectedItem.reset_time }} </span>
                    </div>
                    <div class="flow-right">
                       <span
                           class="number-span">{{ selectedItem.speed_limit > 0 ? selectedItem.speed_limit : ' ' }}
                       <span class="unit-span">M</span></span>
                        <span class="sec-row"></span>
                        <span
                            class="limit-device"> </span>
                    </div>
                </div>
                <div class="card-box-bg speed-box" v-if="trafficArray.length >= 1">
                    <div class="speed-content-box">
                        <div class="speed-line-row" v-for="(item,index) in speedArray" :key="index">
                            <span class="speed-text">{{ item }}</span>
                            <div class="speed-line" v-if="index !== 0"></div>
                        </div>
                    </div>
                    <div class="histogram-container">
                        <div class="x-usage-row" v-for="(item,index) in trafficArray" :key="index">
                            <div class="histogram-box">
                                <div class="histogram"
                                     :class="{'last-histogram': index === (trafficArray.length - 1),'addon-histogram':item.isAddon}"
                                     :style="{height: getHistogramH(item) + 'rem', width: getHistogramW(index,item)  + 'rem'}"></div>
                                <div class="indicator-box" :style="{'padding-left': getUsageP(item,index) + 'rem'}"
                                     v-if="trafficArray.length > 0 && showUsage(item,index)">
                                    <i></i>
                                    <div class="indicator-line" :style="{height: getUsageH() + 'rem'}"></div>
                                </div>
                                <div class="tool-tips-group" :class="{'tool-tips-group-two':trafficArray.length === 1}">
                                    <span v-if="trafficArray.length > 1">{{ item.isAddon ? '' : '' }}{{
                                            index === (trafficArray.length - 1) ? '' : ' '
                                        }} {{
                                            item.trafficLimit
                                        }}G{{
                                            index !== (trafficArray.length - 1) ? '-' + trafficArray[index + 1].trafficLimit + 'G' : ''
                                        }}</span>
                                    <span>{{
                                            (item.speedLimit === '' || item.speedLimit === 'addon') ? '' : '' + item.speedLimit + 'M'
                                        }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="usage-container">
                        <div class="usage-num-box">
                            <span>0G</span>
                            <span>{{
                                    (trafficArray[trafficArray.length - 1].trafficLimit === '' || trafficArray[trafficArray.length - 1].trafficLimit === '0') ? '' : (Math.round(trafficArray[trafficArray.length - 1].trafficLimit / 2) !== trafficArray[trafficArray.length - 1].trafficLimit) ? Math.round(trafficArray[trafficArray.length - 1].trafficLimit / 2) + ' G' : ''
                                }}</span>
                            <span>{{
                                    (trafficArray[trafficArray.length - 1].trafficLimit === '' || trafficArray[trafficArray.length - 1].trafficLimit === '0') ? '' : trafficArray[trafficArray.length - 1].trafficLimit + 'G'
                                }}</span>
                        </div>
                        
                    </div>
                </div>
                <div class="share-reset-bind-box"
                     v-if="selectedItem.status !== 'Inactive' && currentTime <= selectedItem.expired_at">
                    <div class="share-box" :class="{'share-click': isClickShare}" @click="shareService">
                        <i class="share-i"></i>
                        
                    </div>
                    <div class="share-box" :class="{'reset-click': isClickResetService}" @click="resetService">
                        <i class="reset-service-i"></i>
                        
                    </div>
                    <div class="share-box" :class="{'bind-email-click': isClickBindEmail}" @click="bindEmail">
                        <i class="bind-email-i"></i>
                        
                    </div>
                </div>
            </div>
            <ClientTutorial
                v-bind:subUrl="selectedItem.subscriptionUrl !== undefined ? selectedItem.subscriptionUrl : ''"
                v-bind:embedCode="settingsInfo.embedCode"
                v-on:detailedTutorialId="detailedTutorialId"
                v-if="!infoError">
            </ClientTutorial>
        </div>
        <div class="second-parent-box" style="z-index: 5" v-if="detailedUrl.length > 0">
            <DetailedArticle :detailedUrl="detailedUrl"
                             v-on:hideDetailedArticle="detailedUrl = ''"></DetailedArticle>
        </div>
        <MBServiceDialog
            class="dialog"
            @closeDialog="closeDialog"
            v-bind:clickShare="isClickShare"
            v-bind:clickBindEmail="isClickBindEmail"
            v-bind:selectedItem="selectedItem"
            v-if="(isClickShare || isClickBindEmail) && !isPc"></MBServiceDialog>
        <ResetServiceDialog
            class="dialog"
            @closeDialog="closeDialog"
            @bindEmail="bindEmail"
            v-bind:currentUrl="selectedItem.dashboardUrl"
            v-bind:allowBindEmail="settingsInfo.allowBindEmail"
            v-bind:emailStatus="selectedItem.emailStatus"
            v-if="isClickResetService"></ResetServiceDialog>
    </div>
</template>

<script>
import ClientTutorial from "../views/ClientTutorial";
import DetailedArticle from "../views/DetailedArticle";
import ResetServiceDialog from "../dialog/ResetServiceDialog";
import MBServiceDialog from "../dialog/MBServiceDialog";
import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/store/Global";

export default {
    name: "UserServicePanel",
    components: {
        ClientTutorial,
        DetailedArticle,
        ResetServiceDialog,
        MBServiceDialog
    },
    data() {
        return {
            isLogin: localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC),
            isLoading: true,
            isNetworkError: false,
            isShowMessageBox: false,
            infoError: false,
            apiDevelopError: false,
            isClickShare: false,
            isClickResetService: false,
            isClickBindEmail: false,
            isStartBindEmail: false,
            emailErrorMsg: '',
            shareUrl: '',
            emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
            secondNum: 30,
            checkTimer: 0,
            totalCheckCount: 0,
            isCountTimeEnd: false,
            showMsg: false,
            selectedItem: {},
            settingsInfo: {},
            currentTime: new Date().getTime(),
            trafficArray: [{trafficLimit: '', speedLimit: '', isAddon: false, sessionLimit: ''}],
            speedArray: ['', '', '', '', '', ''],
            detailedUrl: '',
            lang: this.$i18n.locale,
            iconLoading: false,
            isPc: this.isPc(),
            pageYOffset: 0
        }
    },
    created() {
        if (this.isLogin) {
            const that = this;
            that.getServiceInfo();
        } else {
            this.isLoading = false;
        }
    },
    methods: {
        closeDialog(result, newUrl) {
            this.isClickResetService = false;
            this.isClickShare = false;
            this.isClickBindEmail = false;
            if (!this.isPc) {
                window.scrollTo({top: this.pageYOffset, left: 0, behavior: 'smooth'});
            }
            if (result) {
                //window.location.href = newUrl;
                const that = this;
                this.request.getAllServices('', '', function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        if (data.data.jjService && data.data.jjService.length > 0) {
                            if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                                data.data.jjService.forEach(item => {
                                    if (item.id === JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id) {
                                        localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(item));
                                        that.getServiceInfo();
                                        that.getSettingInfo();
                                    }
                                });
                            }
                        }
                    }
                });
            }
        },
        detailedTutorialId(id) {
            if (!this.isPc) {
                window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
            }
            this.detailedUrl = `/assets/articles/${id}`;
        },
        getHistogramH(item) {
            if (this.isPc) {
                return 117.5 / ((this.speedArray[1] === '' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '' ? 10 : this.speedArray[1])) * (item.speedLimit === '' ? 10 : item.speedLimit === 'addon' ? (this.speedArray[1] === '' ? 10 : this.speedArray[1]) * (1 + 1 / 4) : item.speedLimit);
            } else {
                return 3.3 / ((this.speedArray[1] === '' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '' ? 10 : this.speedArray[1])) * (item.speedLimit === '' ? 10 : item.speedLimit === 'addon' ? (this.speedArray[1] === '' ? 10 : this.speedArray[1]) * (1 + 1 / 4) : item.speedLimit);
            }
        },
        getHistogramW(index, item) {
            if (this.isPc) {
                return this.trafficArray.length === 1 ? 170 : (index !== (this.trafficArray.length - 1) ? (170 / this.trafficArray[this.trafficArray.length - 1].trafficLimit) * (this.trafficArray[index + 1].trafficLimit - item.trafficLimit) : 25);
            } else {
                return this.trafficArray.length === 1 ? 6.667 : (index !== (this.trafficArray.length - 1) ? (6.667 / this.trafficArray[this.trafficArray.length - 1].trafficLimit) * (this.trafficArray[index + 1].trafficLimit - item.trafficLimit) : 0.6);
            }
        },
        getUsageP(item, index) {
            if (this.isPc) {
                const maxLimit = (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) ? (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024, 3) * 3).toFixed(2) : 500;
                const totalLimit = this.trafficArray[this.trafficArray.length - 1].trafficLimit === '' ? maxLimit : this.trafficArray[this.trafficArray.length - 1].trafficLimit;
                const padding = (170 / totalLimit) * this.getUsage(item, index);
                return padding > 170 ? 50 : padding;
            } else {
                const maxLimit = (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) ? (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024, 3) * 3).toFixed(2) : 6.667;
                const totalLimit = this.trafficArray[this.trafficArray.length - 1].trafficLimit === '' ? maxLimit : this.trafficArray[this.trafficArray.length - 1].trafficLimit;
                const padding = (6.667 / totalLimit) * this.getUsage(item, index);
                return padding > 6.667 ? 0.6 : padding;
            }
        },
        getUsageH() {
            if (this.isPc) {
                return 117.5 / ((this.speedArray[1] === '' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '' ? 10 : (this.speedArray[1] === '' ? 10 : this.speedArray[1])))
                    * (this.selectedItem.speedDeviceLimit[0].speedLimit === '' ? 10 : this.selectedItem.speedDeviceLimit[0].speedLimit === 'addon' ? (this.speedArray[1] === '' ? 10 : this.speedArray[1]) * (1 + 1 / 4) : this.selectedItem.speedDeviceLimit[0].speedLimit);
            } else {
                return 3.3 / ((this.speedArray[1] === '' ? 10 : this.speedArray[1]) / 4 + (this.speedArray[1] === '' ? 10 : (this.speedArray[1] === '' ? 10 : this.speedArray[1])))
                    * (this.selectedItem.speedDeviceLimit[0].speedLimit === '' ? 10 : this.selectedItem.speedDeviceLimit[0].speedLimit === 'addon' ? (this.speedArray[1] === '' ? 10 : this.speedArray[1]) * (1 + 1 / 4) : this.selectedItem.speedDeviceLimit[0].speedLimit);
            }

        },
        getUsage(item, index) {
            if (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) {
                const totalUsage = (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024, 3)).toFixed(2);
                if (index !== this.trafficArray.length - 1) {
                    if (totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(this.trafficArray[index + 1].trafficLimit)) {
                        return totalUsage - parseFloat(item.trafficLimit);
                    }
                } else {
                    if (totalUsage >= parseFloat(item.trafficLimit)) {
                        return totalUsage - parseFloat(item.trafficLimit);
                    } else if (this.trafficArray.length === 1 && item.trafficLimit === '') {
                        return totalUsage;
                    }
                }
                return totalUsage;
            } else {
                return 0;
            }
        },
        showUsage(item, index) {
            if (this.selectedItem.trafficDn !== undefined && this.selectedItem.trafficUp !== undefined) {
                const totalUsage = (parseFloat(this.selectedItem.trafficDn + this.selectedItem.trafficUp) / Math.pow(1024, 3)).toFixed(2);
                if (index !== this.trafficArray.length - 1) {
                    return totalUsage >= parseFloat(item.trafficLimit) && totalUsage < parseFloat(this.trafficArray[index + 1].trafficLimit);
                } else {
                    return totalUsage >= parseFloat(item.trafficLimit) || (this.trafficArray.length === 1 && item.trafficLimit === '');
                }
            } else {
                return false;
            }
        },
        formatDate(value) {
            let date = new Date(value);
            let y = date.getFullYear();
            let MM = date.getMonth() + 1;
            MM = MM < 10 ? ('0' + MM) : MM;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            return y + '-' + MM + '-' + d;
        },
        getSettingInfo() {
            let that = this;
            this.request.getSettingInfo(function (data) {
                if (data && (data.status === 200 || data.status === 201) && data.data) {
                    that.settingsInfo = data.data;
                }
            });
        },
        getServiceInfo() {
            this.trafficArray = [{trafficLimit: '0', speedLimit: 0, isAddon: false, sessionLimit: ''}];
            this.speedArray = ['', '', '', '', '', ''];
            let that = this;
            this.request.getAllServices().then( res => {
                   that.isLoading = false;
                   if (res.code===1){
                  that.selectedItem = res.data ?? [];
                  that.selectedItem.email = that.selectedItem.email ? that.selectedItem.email.endsWith('@UnPanel.net') && that.selectedItem.email.length > 40 ? '' : that.selectedItem.email : '';
                  if ((that.isClickBindEmail && that.isStartBindEmail && that.selectedItem.emailStatus === 'Binded') || localStorage.getItem('verifyEmail')) {
                      that.isStartBindEmail = false;
                      that.emailErrorMsg = '';

                      that.bindEmail();
                      localStorage.removeItem('verifyEmail');

                      that.isShowMessageBox = !that.isShowMessageBox;
                      let msgTimer = setInterval(() => {
                          if (that.isShowMessageBox) {
                              window.clearInterval(msgTimer);
                              that.isShowMessageBox = !that.isShowMessageBox;
                          }
                      }, 2000);
                  }
                } else {
                    /*that.infoError = true;
                    if (data && data.status === 502) {
                        that.apiDevelopError = true;
                    }*/
                }
            });
        },
        shareService() {
            this.pageYOffset = window.pageYOffset
            if (!this.isPc) {
                window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
            }
            this.isClickShare = true;
            this.isClickResetService = false;
            this.isClickBindEmail = false;
        },
        resetService() {
          let that = this;
          that.$confirm({
            title: that.$t('my/service:plan.Text-6'),
            content: that.$t('my/service:plan.Text-7'),
            okText: '',
            cancelText: '',
            onOk() {
              that.request.resetSubscribe().then( res => {
                if(res.code === 1){
                  that.$message.success(res.msg);
                  setTimeout(()=>{
                    window.location.reload();
                  }, 1500);
                } else {
                  that.$message.error(res.msg);
                }
              });
            },
            onCancel() {},
          });
        },
        bindEmail() {
            this.pageYOffset = window.pageYOffset
            if (!this.isPc) {
                window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
            }
            this.isClickBindEmail = true;
            this.isClickResetService = false;
            this.isClickShare = false;
            this.emailErrorMsg = ''
        },
        emailChange() {
            if (this.selectedItem.email) {
                this.emailErrorMsg = ''
            }
        },
        clickBindEmail() {
            if (this.selectedItem.email) {
                if (this.emailReg.test(this.selectedItem.email)) {
                    let that = this;
                    this.$request.bindEmail(this.selectedItem.email, function (data) {
                        if (data.status === 200) {
                            that.isStartBindEmail = true;
                            that.startCountTime();
                            that.emailErrorMsg = '';
                        } else {
                            that.emailErrorMsg = '';
                        }
                    });
                } else {
                    this.emailErrorMsg = 'email';
                }
            } else {
                this.emailErrorMsg = 'email';
            }
        },
        revokeEmail() {
            if (this.checkTimer > 0) {
                window.clearInterval(this.checkTimer);
                this.checkTimer = 0;
                this.totalCheckCount = 0;
            }
            this.isStartBindEmail = false;
            this.selectedItem.email = '';
            this.emailErrorMsg = '';
        },
        clickUnbindEmail() {
            let that = this;
            this.$request.unBindEmail(this.selectedItem.email, function (data) {
                if (data.status === 200) {
                    that.getServiceInfo();
                }
            });
        },
        startCountTime() {
            this.isCountTimeEnd = false;
            let test = setInterval(() => {
                this.secondNum--;
                if (this.secondNum === 0 || this.selectedItem.emailStatus === 'Binded' || !this.isStartBindEmail) {
                    window.clearInterval(test);
                    this.secondNum = 30;
                    this.isCountTimeEnd = true;
                    if (this.selectedItem.emailStatus === 'Binded') {
                        this.isStartBindEmail = false;
                    }
                } else if (this.secondNum === 10) {
                    this.emailErrorMsg = '';
                }
                if (this.secondNum === 25) {
                    if (this.checkTimer === 0) {
                        this.checkTimer = setInterval(() => {
                            this.totalCheckCount++;
                            if (!this.isStartBindEmail || this.selectedItem.emailStatus === 'Binded' || this.totalCheckCount === 15 || this.checkTimer === 0) {
                                window.clearInterval(this.checkTimer);
                                this.checkTimer = 0;
                                this.totalCheckCount = 0;
                            } else {
                                this.getServiceInfo();
                            }
                        }, 4000);
                    }
                }
            }, 1000);
        },
        goToOrderProduct() {
            window.open(`${VUE_SITE}/order/place`, this.$inApp ? '_self' : '_blank');
        },
        goToSupport() {
            window.open(this.settingsInfo.embedCode.CS_CODE, this.$inApp ? '_self' : '_blank');
        },
        goToRenew() {
            const that = this;
            that.$router.push({
                path: '/order/place'
            });
        },
    }
}
</script>

