<template>
    <div class="dialog-container">
        <div class="dialog-bg" :class="{'dialog-bg-pc' : isPc}"></div>
        <div class="dialog-box" :class="{'reset-service-dialog-box': isPc, 'reset-service-dialog-box-mobile': !isPc}" @click="errorMessage = ''">
            <div class="first-row">
                <span>{{resetSuccess ? '' : isResetAccount ? '' : ''}}</span>
                <i @click="closeDialog()"></i>
            </div>
            <div class="loading" v-if="loading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div  class="second-row" v-if="!resetSuccess && !loading">
                <span class="tips-one">{{isResetAccount ? '' : ''}}</span>
                <span class="tips-two"></span>
                <div class="url-box-bg">
                    <div class="left-box">
                        <div class="item-box">
                            <span class="left-span"></span>
                            <span class="right-span">{{isResetAccount ? auAddress : url}}</span>
                        </div>
                        <div class="item-box">
                            <span class="left-span"></span>
                            <span class="right-span">{{isResetAccount ? userAddress: newUrl}}</span>
                        </div>
                    </div>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span></span>
                        </template>
                        <i class="i-copy" @click="copyEvent()"></i>
                    </a-tooltip>
                </div>
                <span class="red-tips-span"></span>
                <span class="blue-tips-span" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && isPc"><span class="bind-email" @click="bindEmail"></span></span>
                <span class="blue-tips-span" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && !isPc"></span>
                <span class="bind-email" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && !isPc" @click="bindEmail"></span>
                <span class="blue-tips-span" v-if="(bindEmailOrNot && emailBindStatus === 'Binded') || auEmailStatus !== 0"></span>
                <div>
                    <a-button class="btn-confirm" @click="resetService" :loading="startSubmit"></a-button>
                </div>
            </div>
            <div class="reset-success-box" v-if="resetSuccess && !loading">
                <span class="tips-one"></span>
                <div class="url-box-bg">
                    <div class="left-box">
                        <div class="left-url-box">
                            <span class="left-span">{{isResetAccount ? '' : ''}}</span>
                            <span class="right-span">{{newUrl}}</span>
                        </div>
                    </div>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span></span>
                        </template>
                        <i class="i-copy" @click="copyEvent()"></i>
                    </a-tooltip>
                </div>
                <div>
                    <a-button class="btn-confirm btn-ok" @click="closeDialog"></a-button>
                </div>
            </div>
            <div class="error-message">
                <span style="position: absolute">{{errorMessage}}</span>
            </div>
        </div>
    </div>
</template>
<script>
import {AU_EMAIL_STATUS, AU_HMAC, AU_UUID} from "@/store/Global";

export default {
    name: "AccessPointDialog",
    data(){
        return {
            auAddress: `${VUE_SITE}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`,
            auEmailStatus: parseInt(localStorage.getItem(AU_EMAIL_STATUS)),
            permissionType: 0,
            errorMessage: '',
            item: {},
            resetSuccess: false,
            startSubmit: false,
            bindEmailOrNot: false,
            emailBindStatus:'',
            url: '',
            newUrl: '',
            copyContent: '',
            userName: '',
            uuid: '',
            isResetAccount: false,
            userAddress: '',
            newUuid: '',
            newHmac: '',
            isPc : this.isPc(),
            loading: true,
        }
    },
    props: {
        currentUrl: {},
        emailStatus: {},
        allowBindEmail:{},
        resetAccount:{},
    },
    watch: {
        resetAccount: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.isResetAccount = value;
                    this.preResetService();
                }
            }
        },
        currentUrl: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.url = value;
                    this.preResetService();
                }
            }
        },
        emailStatus: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.emailBindStatus = value;
                }
            }
        },
        allowBindEmail: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.bindEmailOrNot = value;
                }
            }
        },
    },
    methods:{
        copyEvent() {
            var textarea = document.createElement("textarea");
            textarea.value = this.copyContent;
            textarea.readOnly = true;
            document.body.appendChild(textarea);
            textarea.select(); // 
            document.execCommand("Copy"); // 
            textarea.className = "textarea";
            textarea.style.display = "none";
        },
        preResetService(){
            const that = this;
            if (this.isResetAccount) {
                let param = {
                    "uuid": localStorage.getItem(AU_UUID),
                    "hmac": localStorage.getItem(AU_HMAC),
                };
                this.$request.auRestPreview(param,function (data) {
                    if (data && (data.status === 200 || data.status === 201)) {
                        that.loading = false;
                        that.newUuid = data.data.uuid;
                        that.newHmac = data.data.hmac;
                        that.userAddress = data.data.userAddress;
                        that.copyContent = ': ' + that.auAddress + '\n' + ': ' + that.userAddress;
                    }
                })
            } else {
                this.$request.preResetService(function (data) {
                    if ((data.status === 200 || data.status === 201) && data.data) {
                        that.loading = false;
                        that.userName = data.data.username;
                        that.uuid = data.data.uuid;
                        that.newUrl = data.data.dashboardUrl;
                        that.copyContent = ': ' + that.url + '\n' + ': ' + that.newUrl;
                    } else {
                        if (data && data.message) {
                            that.errorMessage = data.message;
                        } else {
                            that.errorMessage = 'error';
                        }
                    }
                });
            }
        },
        resetService() {
            this.startSubmit = true;
            const that = this;
            if (this.isResetAccount) {
                let param = {
                    "uuid": localStorage.getItem(AU_UUID),
                    "hmac": localStorage.getItem(AU_HMAC),
                    "newUuid": this.newUuid,
                    "newHmac": this.newHmac,
                };
                this.$request.auResetConfirm(param,function (data) {
                    that.startSubmit = false;
                    if ((data.status === 200 || data.status === 201) && data.data) {
                        that.resetSuccess = true;
                        localStorage.setItem(AU_UUID, data.data.uuid);
                        localStorage.setItem(AU_HMAC, data.data.hmac);
                        that.$emit('closeDialog',that.resetSuccess);
                    } else {
                        if (data && data.message) {
                            that.errorMessage = data.message;
                        } else {
                            that.errorMessage = 'error';
                        }
                    }
                });
            } else {
                this.$request.resetService(this.userName,this.uuid,function (data) {
                    that.startSubmit = false;
                    if (data.status === 200 && data.data) {
                        that.resetSuccess = true;
                        that.newUrl = data.data.dashboardUrl;
                        that.copyContent = ': ' + that.newUrl;
                    } else {
                        if (data && data.message) {
                            that.errorMessage = data.message;
                        } else {
                            that.errorMessage = 'error';
                        }
                    }
                });
            }
        },
        bindEmail() {
            this.$emit('bindEmail',true);
        },
        closeDialog(){
            this.errorMessage = '';
            this.$emit('closeDialog',this.resetSuccess,this.newUrl)
        }
    }
}
</script>

